<template>
	<view>
		<view style="height: calc(100vh - 80px); overflow-y: auto;">
			                
			<view v-for="msg in chatMsg" :key="msg.index" style="margin: 10px 0; display: block;">
				<image v-if="msg.user == 'listener'" style="float: left; width: 60px; height: 60px; background-color: #eeeeee; border-radius: 50%;" :src="msg.avatar"></image>
				<text v-if="msg.user == 'listener'" class="text">{{new Date(msg.time).toLocaleDateString()}}</text>
				<text v-if="msg.user == 'listener'" class="text">{{msg.content}}</text>
				
				<image v-if="msg.user == 'me'" style="float: right; width: 60px; height: 60px; background-color: #eeeeee; border-radius: 50%;" :src="msg.avatar"></image>
				<text v-if="msg.user == 'me'" class="text2">{{new Date(msg.time).toLocaleDateString()}}</text>
				<br>
				<text v-if="msg.user == 'me'" class="text2">{{msg.content}}</text>		
			</view>
		</view>
		<view class="bottom">
			<input class="uni-input" v-model:value="me.msg" style="display: inline-block; width:70%; margin: 18px; background-color: white; border-radius: 10px;"/>
			<button class="mini-btn" @tap="sendMsg" type="primary" size="mini" style="float: right; margin: 13px; position: absolute; bottom: 0">发送</button>
		</view>

	</view>
</template>

<script>
	export default {
		data() {
			return {
				me: {
					msg: ""
				},
				chatMsg: [
					{
						avatar: "../../static/imgs/guy.png",
						content: "hi",
						user: "listener",
						time: 1350052653000
					}			
				]
			}
		},
		onLoad(options) {
			uni.setNavigationBarTitle({
			            title: options.chatWith
			});
		},	
		onPageScroll: function(Object) {
			console.log(Object.scrollTop+"-----------------------------------");
		},
		methods: {
			switchTo() {
				   uni.navigateTo({
							url: '/pages/one2one/index',
					});
			},
			sendMsg() {
				let that = this;
				that.chatMsg.push(					{
						avatar: "../../static/imgs/my.png",
						content: that.me.msg,
						user: "me",
						time: 1350052653000
				})
				console.log("_________________________________________________")
				console.log(that.me.msg)
				let url = 'http://api.qingyunke.com/api.php?key=free&appid=0&msg=' + that.me.msg;
				uni.request({
				    url: url, 
				    success: (res) => {
				        let msg = res.data.content;
						that.chatMsg.push(					{
								avatar: "../../static/imgs/guy.png",
								content: msg,
								user: "listener",
								time: 1350052653000
						})
				    }
				});
			}
		}
	}
</script>

<style>
	.container {
		overflow-x: hidden;
	}
	.text {
		color: gray; 
		font-size: 16px; 
		position: relative; 
		left: 20px; 
		top: 5px;
		display: block;
	}
	.text2 {
		color: gray; 
		font-size: 16px; 
		position: relative; 
		left: 270px; 
	}
	.bottom {
		background-color: #CCCCCC; 
		width: 100vw; 
		height: 60px; 
		position: fixed;
		bottom: 0; 
	}
</style>
